<template>
  <div>
    <header class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span style="margin-left: 0.4rem">领取礼包</span>
      <p style="font-size: 0.28rem" @click="goBack">领取记录</p>
    </header>
    <div class="fans">
        <section class="fans_z">
          <p>直推10人可获得大礼包一份</p>
          <a href="javascript:void(0)" class="fans_btn" @click="drawPackage(2,packState.zt_number)" v-if="packState.zt_number !== 2">领取</a>
          <a href="javascript:void(0)" class="fans_btn fans_btn_y" v-if="packState.zt_number === 2">已领取</a>
        </section>
      <section class="fans-c" v-if="packState.type >= 1">
          <p class="fans-c-t">
            <img src="../../../static/shop/fans.png" alt="">
            <span  class="fans_title">{{info[0].name}}</span>
          </p>
          <section class="fans_rule">
            <div>
              <a href="javascript:void(0)" class="fans_btn" @click="drawPackage(1,packState.number,1)" v-if="packState.number !== 2">领取</a>
              <a href="javascript:void(0)" class="fans_btn fans_btn_y" v-if="packState.number === 2">已领取</a>
            </div>
          </section>
        </section>
      <section class="fans-c"  v-if="packState.type >= 2">
        <p class="fans-c-t">
          <img src="../../../static/shop/star_p.png" alt="">
          <span  class="fans_title fans1">{{info[1].name}}</span>
        </p>
        <section class="fans_rule">
          <div>
            <a href="javascript:void(0)" class="fans_btn" @click="drawPackage(1,packState.daren_number,2)" v-if="packState.daren_number !== 2">领取</a>
            <a href="javascript:void(0)" class="fans_btn fans_btn_y" v-if="packState.daren_number === 2">已领取</a>
          </div>
        </section>
      </section>
      <section class="fans-c" v-if="packState.type === 3">
        <p class="fans-c-t">
          <img src="../../../static/shop/heart.png" alt="">
          <span  class="fans_title fans2">{{info[2].name}}</span>
        </p>
        <section class="fans_rule">
          <div>
            <a href="javascript:void(0)" class="fans_btn" @click="drawPackage(1,packState.aixin_number,3)" v-if="packState.aixin_number !== 2">领取</a>
            <a href="javascript:void(0)" class="fans_btn fans_btn_y" v-if="packState.aixin_number === 2">已领取</a>
          </div>
        </section>
      </section>
    </div>
  </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  export default {
    data(){
      return {
        packState:{},
        info: {}
      }
    },
    methods:{
      goBack(){
        this.$router.push('/packRecord');
      },
      back(){
        this.$router.go(-1);
      },
      // 获取状态
      getPackageState(){
        this.post('home/User/getLibaoState', {}).then(res => {
          if (res.data.code === 10000) {
            this.packState = res.data.info;
          }
        })
      },
      drawPackage(type, number, val){
        // type 1 开店礼包 2 直推礼包
        if (type === 1) {
          if (number === 1) {
            let url = `/delivery/1/${val}`;
            this.$router.push(url)
          } else if (number === 0){
            Toast({
              message: '开店礼包暂时无法领取',
              duration: 1000
            });
          } else {
            Toast({
              message: '开店礼包已经领取',
              duration: 1000
            });
          }
        } else {
          //  直推
          if (number === 1) {
            let url = '/delivery/2/0';
            this.$router.push(url)
          } else if (number === 0){
            Toast({
              message: '直推礼包暂时无法领取',
              duration: 1000
            });
          }else {
            Toast({
              message: '直推礼包已经领取',
              duration: 1000
            });
          }
        }
      }
    },
    mounted(){
      this.getPackageState();
      this.post('home/Shop/getDianpu', {}).then(res  => {
        if (res.data.code === 10000) {
          this.info = res.data.info;
      }
      })
    }
  }
</script>
<style scoped>
  .header {
    width: 100%;
    height: 0.88rem;
    display: flex;
    align-items: center;
    padding:0 0.2rem;
  }
  .count_top {
    background-color: #FFFFFF;
    justify-content: space-around;
  }
  .count_top img:first-child {
    width: 0.2rem;
  }
  .count_top img:last-child {
    width: 0.4rem;
  }

  .count_top span {
    flex: 1;
    font-size: 0.3rem;
    color: #111111;
    text-align: center;
  }
  .fans {
    height: calc(100vh - 0.88rem);
    overflow: hidden;
    position: relative;
    background-color: #f2f2f2;
    padding:0 0.2rem;
  }
  .fans-c {
    margin: 0.2rem 0;
    padding: 0.3rem;
    background-color: #ffffff;
    border-radius: 0.1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .fans-c-t img {
    width: 0.5rem;
    margin-right: 0.05rem;
    vertical-align: top;
    margin-top: 1px;
  }
  .fans-c-t span {
    font-size: 0.34rem;
    color: #111111;
    position: relative;;
  }
  /*颜色*/
  /*.fans_title:before {*/
    /*display: inline-block;*/
    /*content: '';*/
    /*width: 1.3rem;*/
    /*height: 0.06rem;*/
    /*background-color: #736ae6;*/
    /*position: absolute;*/
    /*bottom: 0.08rem;*/
    /*opacity: 0.4;*/
  /*}*/
  /*.fans1:before {*/
    /*background-color: #d53fe7;*/
    /*opacity: 0.4;*/
  /*}*/
  /*.fans2:before {*/
    /*background-color: #ff0078;*/
    /*opacity: 0.4;*/
  /*}*/
  .fans_rule {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .fans-l {
    padding: 0 0.78rem;
  }
  .fans-l h2 {
    font-size: 0.3rem;
    color: #111111;
    margin-bottom: 0.2rem;
  }
  .fans-l p {
    font-size: 0.26rem;
    color: #444444;
    line-height: 0.32rem;
  }
  .fans_btn {
    display: inline-block;
    width: 1.4rem;
    height: 0.58rem;
    color: #ffffff;
    font-size: 0.28rem;
    background-color: #ff6f00;
    border-radius: 0.1rem;
    text-align: center;
    line-height: 0.58rem;
  }
  .fans_z {
    margin-top: 0.2rem;
    padding: .3rem 0.3rem 0.3rem 1rem;
    background-color: #ffffff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 0.08rem;
  }
  .fans_z p {
    font-size: 0.28rem;
    color: #111111;
    line-height: 0.36rem;
  }
  .fans_btn_y {
    background-color: #b6b6b6;
  }
</style>



